@import "src/common/style/sass/variables.module";

.footerBlock {
  background: $primaryColor;
  height: 80px;

  .containerFooter {
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

    .footerHeading {
      margin: 0;
    }
    .linkContainer {
      display: flex;
      border: 1px solid;
      justify-content: space-between;
      max-width: 300px;
      width: 100%;
      height: 50px;

      .icon {
        background: white;
        height: 50px;
        width: 50px;
      }
    }
  }
}




