.navLink {
    color: #000;
    margin-right: 0;
    padding: 0;
    letter-spacing: 1px;
    position: relative;
    font-size: 16px;
    text-transform: uppercase;
    opacity: .5;
    font-weight: 600;
    text-decoration: none;
    font-family: "Open Sans", sans-serif;
    cursor: pointer;
    margin-left: 40px;
}

.navLink:hover {
    color: #00b074;
}

.list li {
    list-style: none;
    z-index: 2;
}


.list {
    display: flex;
}

@media (max-width: 767px) {
    .navContainer {
        position: fixed;
        display: flex;
        top: -100%;
        left: 0;
        width: 100%;
        min-height: 50%;
        background-color: #ffffff;
        padding: 80px 0px 0px 0px;
        transition: all 0.5s ease 0s;
        justify-content: center;
        text-align: center;
    }

    .navContainer.active {
        top: 0;
        transition: all 0.5s ease 0s;
    }

    .list {
        display: block;
        z-index: 2;

    }

    .list li {
        margin: 0 0 30px 0;
        z-index: 4;
    }

    .navLink {
        margin: 0;
        font-size: 30px;
        color: #00b074;
        opacity: 1;
    }

    .navLink:hover {
        color: #000;
        opacity: 0.5;
    }
}