@import "src/common/style/sass/variables.module";

.skill {
    width: 250px;
    height: 250px;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 15px 10px;
    border-radius: 10px;
    gap: 3px;

}
.skill:hover {
    box-shadow: $primaryColor -1px 3px 60px 10px;

}

.skill h3 {
    margin: 0;
}

.skill p{
    margin: 20px 0 0 0;
}

.icon {
    padding: 10px;
    width: 80px;
    height: 80px;
    border: $primaryColor 1px solid;
    border-radius: 10px;
    background: $primaryColor;
}

.description{
    color: $textNavColor;
}

.titleSkill{
    position: relative;
    z-index: 0;
}

.titleSkill::before {
    content: "";
    width: 30px;
    height: 3px;
    bottom: -5px;
    background: $primaryColor;
    position: absolute;
    left: 50%;
    -webkit-transform: translate(-50%, 50%);
}

@media screen and (max-width: 420px) {
    .skill{
        width: 100%;
    }
    }

