.skillsBlock {
    min-height: 40vh;
    box-sizing: border-box;
    background: #2b3940;
    padding: 50px 0;
}

.skillsContainer {
    flex-direction: column;
    justify-content: flex-start;
}

.skills {
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: 20px;
}

.title {
    background: white;
}