@import "src/common/style/sass/variables.module";

.title {
  font-size: 20px;
  font-weight: 700;
  line-height: 32px;
  letter-spacing: normal;
  margin: 0 0 40px;
  position: relative;
  z-index: 0;

  h2 {
    margin: 0;
  }

  &::before {
    content: "";
    width: 60px;
    height: 3px;
    bottom: -10px;
    background: $primaryColor;
    position: absolute;
    left: 50%;
    -webkit-transform: translate(-50%, 50%);
  }
}



