.mainBlock {
    min-height: 100vh;
    background-color: #e2e2e2;
    padding-top: 100px;
}

@media (max-width: 767px) {
    .mainBlock {
        background-color: #e2e2e2;
        padding-top: 100px;
        opacity: 1;
    }
    .mainBlock.active:before {
        position: absolute;
        content: "";
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: black;
        opacity: 0.5;
        visibility: visible;
        transition-delay: 0s;
    }
}

.text {
    overflow: hidden;
    width: 450px;
}

.photo {
    margin-top: 20px;
    width: auto;
    height: 550px;
    padding: 7px;
    border-radius: 30px 0 30px 0;
    box-shadow: #4d4d4d 12px 12px 10px;
    background: #00b074;
}

.heading {
    font-size: 25px;
    color: #4d4d4d;
    opacity: 1;
    margin: 0 0 8px;
}

.name {
    font-size: 50px;
    text-transform: capitalize;
    font-weight: 900;
    margin-bottom: 15px;
    margin-top: 10px;
}

@media screen and (max-width: 500px ) {
    .photo {
        margin: 0 0 20px 0;
        width: 100%;
        height: 100%;
    }

    .name {
        text-align: center;
    }

    .heading {
        text-align: center;
    }
}

