.contactsBlock{
    height: 70vh;
    background: #2b3940;
    padding: 100px 0;
}

.container{
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
}

.contactsForm{
    display: flex;
    gap: 20px;
    align-items: center;
    max-width: 300px;
    width: 100%;
    height: 300px;
    flex-direction: column;
}

.textarea{
    width: 100%;
    height: 150px;
}

.input{
    width: 100%;
    height: 30px;
}
.send{
    background: white;
}

.title{
    background: white;
}

@media screen and (min-width: 768px) {
    .contactsForm{
        max-width: 500px;
    }
}