@import "src/common/style/sass/variables.module";

.project {
  border-radius: 10px;
  background: $primaryColor;
  width: 295px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  .imgContainer {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width: 100%;
    min-height: 250px;
    justify-content: center;
    display: flex;
    align-items: center;
    border-radius: 10px 10px 0 0;
    position: relative;
    z-index: 0;
    &::before {
      border-radius: 10px 10px 0  0;
      position: absolute;
      content: "";
      background-color: black;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      opacity: 0.7;
    }
    .link {
      background: $textColor1;
      align-items: center;
      position: relative;
      z-index: 0;
    }
  }

  .textProject {
    min-height: 100px;
    width: 100%;
    color: $textColor1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    margin: 20px 0;
  }
  .textProject h2 {
   margin: 0;
  }

  .description {
    max-width: 250px;
    min-height: 30px;
    word-wrap:break-word;
    justify-content: center;
    display: inline-block;
    text-align: center;
  }
  @media screen and (min-width: 1417px) {
      width: 450px;
  }
  @media screen and (max-width: 320px) {
      width: 100%;
  }
}






