.header{
    position: fixed;
    width: 100%;
    top:0;
    left:0;
    z-index: 30;
}

.headerBody{
    position: relative;
    display: flex;
    justify-content: space-between;
    height: 80px;
    align-items: center;
}

.header:before{
    content: "";
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #e2e2e2;
    z-index: 2;
}

.container{
    height: 100%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: end;
    flex-wrap: wrap;
}

.burger{
    display: none;
}

@media (max-width:767px) {
    .headerBody{
        height: 50px;
        justify-content: end;
    }
    .burger{
        display: block;
        position: relative;
        width:  30px;
        height: 20px;
        z-index: 3;
    }
    .burger:before,
    .burger:after{
        content: "";
        position: absolute;
        width: 100%;
        height: 2px;
        background-color:  #00b074;
        left: 0;
        transition: all 0.1s ease 0s;
    }
    .burger:before{
        top:0;
    }
    .burger:after{
        bottom: 0;
    }
    .burger span{
        position: absolute;
        background-color:  #00b074;
        left: 0;
        width: 100%;
        height: 2px;
        top:9px;
        transition: all 0.1s ease 0s;
    }
}

.burger.active:before{
    transform: rotate(45deg);
    top: 9px;
}
.burger.active span{
    transform: scale(0);
}
.burger.active:after{
    transform: rotate(-45deg);
    bottom: 9px;
}